import React from 'react';
import Link from 'next/link'
import css from './../../css/services/servicesRow.scss'

export default ({type, text, link, price}) => {
    return(
        <div className={css.servicesRow}>
            <h2>{text}</h2>
            <span className={(type==='price') ? css.servicesRowPrice : css.hide}>
                {price}
            </span>
            <a className={(type==='link') ? css.servicesRowLink : css.hide} href={link}> 
            </a>
        </div>
    )
}